import React from 'react'
import PropTypes from 'prop-types'
import { withRouter } from 'next/router'
import { TweenMax, Power2 } from 'gsap'
import { withI18next } from '../../lib/withI18next'
import Fireflies from './Fireflies'
import { HomeContainer } from './style.js'

class Home extends React.Component {
  static propTypes = {
    t: PropTypes.any
  }

  // static async getInitialProps ({ query, res }) {
  //   // await new Promise((resolve) => {
  //   //   setTimeout(resolve, 3000)
  //   // })
  //   return {}
  // }

  constructor (props) {
    super(props)
    // console.log(props)

    this.state = {
      h1Text1: this.props.t('welcome')
    }
  }

  componentDidMount () {
    let pageItem = document.querySelectorAll('.pageItem')
    TweenMax.set('.pageContainer', { width:window.innerWidth * pageItem.length, height:window.innerHeight })
    TweenMax.set(pageItem, { width:window.innerWidth, height:window.innerHeight })
    for (let i = 0; i < pageItem.length; i++) {
      let active = false
      for (let j = 0; j < pageItem[i].classList.length; j++) {
        if (pageItem[i].classList[j] === 'active') {
          active = true
        }
      }
      if (!active) {
        TweenMax.set(pageItem[i], { left: -window.innerWidth })
      }
    }
    setTimeout(() => {
      TweenMax.to(pageItem[0], 2, { left:window.innerWidth, ease:Power2.out })
      TweenMax.to(pageItem[1], 2, { left:0, ease:Power2.out })
    }, 2000)

    TweenMax.set('.intro h1', { x:20, y:-10, rotationX:5, rotationY:-5, scale:0.85, autoAlpha:0, transformPerspective:200, transformOrigin:'100% 100%' })
    TweenMax.set('.intro h2, .intro h3', { y:10, rotationX:-30, scale:0.95, autoAlpha:0, transformPerspective:1000, transformOrigin:'50% 100%' })
    TweenMax.staggerTo('.intro h1', 1, { x:0, y:0, rotationX:0, rotationY:0, scale:1, autoAlpha:1, ease:Power2.easeOut, delay:1 }, 0.7)
    TweenMax.staggerTo('.intro h2, .intro h3', 1, { y:0, rotationX:0, scale:1, autoAlpha:1, ease:Power2.easeOut, delay:2.2 }, 0.3)

    let text = ''
    for (let i = 0; i < this.props.t('welcome').length; i++) {
      let letter = this.props.t('welcome')[i]
      text += '<span class="' + letter.toLowerCase() + i + '">' + letter + '</span>'
    }
    this.setState({ h1Text1: text }, () => {
      TweenMax.set('.intro h1 span', { x:-40, y:-40, rotationX:40, autoAlpha:0 })
      TweenMax.staggerTo('.intro h1 span', 1, { x:0, y:0, rotationX:0, autoAlpha:1, ease:Power2.easeOut, delay:1.2 }, 0.1)
    })
  }

  render () {
    const { t } = this.props

    return (
      <HomeContainer className='pageContainer'>
        <div className='pageItem active'>
          <div className='innerPage'>
            <Fireflies />
            <div className='intro'>
              <h1 className='font-raleway'><span className='welcome' dangerouslySetInnerHTML={{ __html: this.state.h1Text1 }} /></h1>
              {/* <h1 className='font-raleway'><span>{t('welcome')}</span></h1> */}
              <h2 className='font-ralewayBold'><span dangerouslySetInnerHTML={{ __html: t('h2') }} /></h2>
              <h3 className='font-ralewayBold'><span>{t('h3')}</span></h3>
            </div>
          </div>
        </div>
        <div className='pageItem'>
          <div className='innerPage'>
            about
          </div>
        </div>
      </HomeContainer>
    )
  }
}

export default withI18next(['home', 'common'])(withRouter(Home))
